<template>
  <div class="topic-list">
    <!--  搜索  -->
    <el-form label-position="left" label-width="100px" :model="topic" inline>
      <template v-if="isOpen">
        <el-form-item label="题型" class="search-item">
          <el-select v-model="topic.type" clearable placeholder="请选择">
            <el-option
              v-for="item in typeList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="创建时间" class="search-item">
          <el-date-picker
            v-model="topic.time"
            type="daterange"
            align="right"
            unlink-panels
            range-separator="-"
            start-placeholder="创建时间"
            end-placeholder="创建时间"
            value-format="yyyy-MM-dd"
          />
        </el-form-item>
        <el-form-item label="试题状态" class="search-item">
          <el-select v-model="topic.stage" clearable placeholder="请选择">
            <el-option
              v-for="item in stageList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
      </template>
      <el-form-item label="单节课名称" class="search-item">
        <el-select v-model="topic.name" clearable placeholder="请选择">
          <el-option
            v-for="item in singleList"
            :key="item._id"
            :label="item.title"
            :value="item._id"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="单节课状态" class="search-item">
        <el-select v-model="topic.status" clearable placeholder="请选择">
          <el-option
            v-for="item in statusList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
      <template v-if="isOpen">
        <el-form-item label="试题难度" class="search-item">
          <el-select v-model="topic.difficult" clearable placeholder="请选择">
            <el-option
              v-for="item in difficultyList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="创建人" class="search-item">
          <el-select v-model="topic.createName" clearable placeholder="请选择">
            <el-option
              v-for="item in createNameList"
              :key="item._id"
              :label="item.name"
              :value="item._id"
            />
          </el-select>
        </el-form-item>
      </template>
      <el-form-item>
        <el-button type="primary" class="select-btn" @click="onSubmit"
          >查询</el-button
        >
      </el-form-item>
      <el-button type="text" class="arrow" @click="isOpen = !isOpen">
        {{ isOpen ? '收起' : '展开' }}
        <i
          :class="isOpen ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
          style="font-size: 16px;color: #0085FF"
        />
      </el-button>
    </el-form>
    <!--  列表  -->
    <div class="list">
      <div class="title">
        <div>试题列表</div>
        <el-button type="primary" @click="$router.push({ name: 'addTopic' })"
          >添加试题</el-button
        >
      </div>
      <el-table :data="list" style="width: 100%" :empty-text="hasListInfo">
        <el-table-column
          fixed
          prop="num"
          label="序号"
          min-width="80"
          align="center"
        />
        <el-table-column prop="id" label="编号" min-width="100" align="center">
          <template slot-scope="scope">
            <el-popover
              placement="top-start"
              trigger="hover"
              :content="scope.row.id"
            >
              <span slot="reference" class="cell" v-html="scope.row.id" />
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column
          prop="question_content"
          label="试题内容"
          min-width="120"
          align="center"
        >
          <template slot-scope="scope">
            <el-popover
              placement="top-start"
              trigger="hover"
              :content="scope.row.question_content"
            >
              <span
                slot="reference"
                class="cell"
                v-html="scope.row.question_content"
              />
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column
          prop="question_type"
          label="题型"
          min-width="80"
          align="center"
        />
        <el-table-column
          prop="single_name"
          label="单节课名称"
          min-width="120"
          align="center"
        />
        <el-table-column
          prop="single_status"
          label="单节课状态"
          min-width="100"
          align="center"
        />
        <el-table-column
          prop="question_status"
          label="试题状态"
          min-width="80"
          align="center"
        >
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.question_status"
              @change="setStatus(scope.row)"
            />
          </template>
        </el-table-column>
        <el-table-column
          prop="difficult"
          label="难度"
          min-width="80"
          align="center"
        />
        <el-table-column
          prop="create_name"
          label="创建人"
          min-width="80"
          align="center"
        />
        <el-table-column
          prop="create_time"
          label="创建时间"
          min-width="120"
          align="center"
        />
        <el-table-column fixed="right" label="操作" width="150" align="center">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="edit(scope.row)"
              >编辑</el-button
            >
            <el-button
              type="text"
              size="small"
              @click="
                delShow = true
                rowInfo = scope.row
              "
              >删除</el-button
            >
            <el-button
              type="text"
              size="small"
              @click="
                $router.push(`/learning-admin/topic/see-topic/${scope.row.id}`)
              "
              >查看</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          :current-page="currentPage"
          :page-sizes="[12, 18, 24]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="count"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
      <el-dialog
        title="删除提示"
        :visible.sync="delShow"
        width="30%"
        center
        top="30vh"
      >
        <p class="center">删除后，该试题信息将不再保留，请确定是否删除？</p>
        <span slot="footer" class="dialog-footer">
          <el-button @click="delShow = false">取 消</el-button>
          <el-button type="primary" @click="confirm">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import {
  getQuestionList,
  delQuestion,
  getAllSingle,
  userAll,
  getQuestionStatus
} from '@/api/learningAdmin'
export default {
  data() {
    return {
      isOpen: false,
      list: [],
      currentPage: 1,
      delShow: false,
      pageSize: 12,
      count: 0,
      rowInfo: '',
      hasListInfo: '暂无数据',
      singleList: [],
      createNameList: [],
      topic: {
        type: '',
        name: '',
        stage: '',
        time: ['', ''],
        status: '',
        difficult: '',
        createName: ''
      },
      typeList: [
        { value: 'all', label: '全部' },
        { value: 'radio', label: '单选题' },
        { value: 'checkbox', label: '多选题' },
        { value: 'short_answer', label: '简答题' }
      ],
      stageList: [
        { value: 'all', label: '全部' },
        { value: 'enable', label: '启用' },
        { value: 'disable', label: '禁用' }
      ],
      statusList: [
        { value: 'all', label: '全部' },
        { value: 'enable', label: '计划使用中' },
        { value: 'disable', label: '计划未使用' }
      ],
      difficultyList: [
        { value: 'all', label: '全部' },
        { value: '1', label: '简单' },
        { value: '2', label: '正常' },
        { value: '3', label: '难' }
      ]
    }
  },
  computed: {
    getDifficult() {
      return function(num) {
        if (num === '1') return '简单'
        if (num === '2') return '正常'
        if (num === '3') return '难'
      }
    }
  },
  created() {
    this.getQuestionList()
    this.getAllSingle()
    this.getAllUser()
  },
  methods: {
    onSubmit() {
      this.currentPage = 1
      this.getQuestionList(this.topic)
    },
    edit(row) {
      this.$router.push('/learning-admin/topic/edit-topic/' + row.id)
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getQuestionList(this.topic)
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getQuestionList(this.topic)
    },
    async confirm() {
      const { data, code } = await delQuestion(this.rowInfo.id)
      if (code === 20000 && data.result) {
        this.$message({ message: '删除成功', type: 'success' })
        this.getQuestionList(this.topic)
        this.delShow = false
      }
    },
    async getQuestionList(search) {
      // 过去试题列表
      let params
      if (search) {
        params = {
          pageSize: this.pageSize,
          page: this.currentPage
        }
        if (search.time[0] && search.time[1]) {
          params.started_at = search.time[0]
          params.ended_at = search.time[1]
        }
        if (search.type && search.type !== 'all') params.type = search.type
        if (search.difficult && search.difficult !== 'all') {
          params.difficult = parseInt(search.difficult)
        }
        if (search.stage && search.stage !== 'all') {
          params.status = search.stage
        }
        if (search.status && search.status !== 'all') {
          params.plan_status = search.status
        }
        if (search.createName) params.creator = search.createName
        if (search.name) params.catalog = search.name
        this.hasListInfo = '暂无数据，请重新输入关键词'
      } else {
        params = {
          pageSize: this.pageSize,
          page: this.currentPage
        }
        this.hasListInfo = '暂无数据'
      }
      const { data, code } = await getQuestionList(params)
      if (code === 20000) {
        this.count = data.count
        this.list = data.list.map((item, index) => {
          return {
            num: (data.page - 1) * data.pageSize + index + 1,
            id: item._id,
            question_content: item.stem.replace(/<\/?.+?>/g, ''),
            question_type:
              item.type === 'radio'
                ? '单选题'
                : item.type === 'checkbox'
                ? '多选题'
                : '简答题',
            single_name: item.catalog.title,
            single_status:
              item.plan_status === 'disable' ? '计划未使用' : '计划使用中',
            question_status: item.status === 'enable',
            difficult: this.getDifficult(item.difficult),
            create_name: item.creator ? item.creator.name : '',
            create_time: item.created_at.split('T')[0]
          }
        })
      }
    },
    async getAllSingle() {
      const { code, data } = await getAllSingle()
      if (code === 20000) {
        this.singleList = data.list
      }
    },
    async getAllUser() {
      const { code, data } = await userAll()
      if (code === 20000) this.createNameList = data
    },
    setStatus(row) {
      getQuestionStatus(row.id)
    }
  }
}
</script>

<style lang="scss" scoped>
.topic-list {
  display: flex;
  flex-wrap: wrap;
  font-size: 14px;
  margin-bottom: 20px;
  /deep/form {
    width: 100%;
    .el-date-editor {
      padding: 0 10px;
      width: 220px;
      height: 32px;
      .el-range-input {
        width: 85px;
        height: 30px;
      }
    }
  }
  .search-item {
    margin-right: 70px;
    color: #17233d;
    /deep/input {
      width: 220px;
      height: 32px;
    }
    /deep/span {
      top: -3px;
    }
    /deep/.el-input__suffix {
      top: 0;
    }
  }
  .select-btn {
    padding: 0 20px;
    width: 68px;
    height: 32px;
  }
  .arrow {
    float: right;
  }
  .list {
    margin: 20px 0;
    padding: 20px;
    width: 100%;
    border: 1px solid #ebeef5;
    .title {
      display: flex;
      justify-content: space-between;
      line-height: 32px;
      /deep/button {
        padding: 6px 20px;
        height: 32px;
      }
    }
    /deep/.el-dialog__wrapper {
      button {
        padding: 6px 20px;
        height: 32px;
      }
      .el-dialog__headerbtn {
        top: 10px;
        right: 5px;
      }
    }
    .pagination {
      margin-top: 50px;
      text-align: center;
    }
  }
  /deep/.cell {
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
    *:not(button) {
      display: inline-block;
      width: 100%;
    }
  }
  .center {
    text-align: center;
  }
}
</style>
